@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');
.custom-body{
 
    background-image: linear-gradient(white, rgb(90, 186, 218));
    background-image: linear-gradient(to right,(rgb(222, 221, 221), #aae0e7));
    height: 700px;
    display: flex;
    justify-content: center;
    
}
.custom-header-name{
    font-family: 'Noto Serif', serif;
    color: #7d7d7dc7;
    font-weight: 400;
    font-size: 40px;
}
.custom-model{
   
    background-color: white;
    height: 470px;
    width: 350px;
    border-radius: 50px;
 
}
.custom-input{
    background-color: white;
    border-radius: 50px;
    width: 220px;
    padding: 10px;
    margin-right: 3px;
    margin-bottom: 20px;
    font-size: 15px;
    color: #505353;
}
.custom-btn{
    background-color: white;
    width: 98px;
    padding: 10px;
    margin-right: 5px;
    margin-bottom: 20px;
    font-size: 15px;
    color: #505353;
    border-radius: 50px;
}
.costom-name{
    display: flex;
    justify-content: center;
    font-size: 22px;
    font-family: 'Lato', sans-serif;
    color: #7d7d7dc7;
    font-weight: 800;
    margin-bottom: -25px;
  
}
.custom-tem{
    padding-top: 5px;
    font-size:40px;
    margin-left: 10px;
    font-family: 'Lato', sans-serif;
    color: #7d7d7dc7;
   
}
.custom-des{
    font-size:25px;
    margin-left: 10px;
    font-family: 'Lato', sans-serif;
    color: #444242c7;
    font-weight: 500;
}

.custom-weather-icon{
    display: flex;
    justify-content: center;
    font-family: 'Lato', sans-serif;
    border-radius: 100%;
    font-size: 100px;
    margin-top: -10px;
    color: #7d7d7dc7;
   
  }
  .custom-Tem-m{
    font-size:16px;
    margin-top: -15px;
    margin-left: 10px;
    font-family: 'Lato', sans-serif;
    color: #7d7d7dc7;
    
  }
  .custom-time-p{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size:30px;
    margin-top:30px;
    font-family: 'Lato', sans-serif;
    color: #373737c7;
    font-weight:700;    

  }
  .custom-error-p{
   padding-top: 50px;
   margin-right: 20px;
   margin-left: 20px;
    /* display: flex;
    justify-content: center;
    align-items: center; */
    color: #373737c7;
  }
  .custom-icon{
   font-size: 120px !important;
   margin-left: 100px;
   margin-top: 50px;

  }